<template>
  <div class="header">
    <div @click="correctRedirect">
      <img class="logo" src="../../assets/imgs/logo.svg" />
    </div>
    <div class="d-flex align-items-center nav pe-0 pe-lg-5" style="gap: 10px">
      <div class="c-p nav-text" @click="goTo('/news')">
        <i class="bi bi-newspaper"></i> Новости
      </div>
      <template v-if="getRole === 'user'">
        <template v-if="isCanUseTools">
          <div class="c-p nav-text" @click="goTo('/lessons')">
            <i class="bi bi-award-fill"></i> Вебинары
          </div>
          <div class="c-p nav-text" @click="goTo('/testing')">
            <i class="bi bi-bank"></i> Тестирование
          </div>
        </template>
        <div class="bell">
          <i class="bi bi-bell"></i>
        </div>
        <div v-if="isAuth" class="user" @click="goTo('/profile')">
          <i class="bi bi-person-circle"></i>
        </div>
      </template>
      <template v-if="getRole === 'employee'">
        <div class="c-p nav-text" @click="goTo('/bid')">
          <i class="bi bi-award-fill"></i> Заявки
        </div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    isAuth() {
      return this.$store.getters["user/isAuth"];
    },
    isCanUseTools() {
      return this.$store.getters["user/isUserAploadDocument"];
    },
    getRole() {
      return this.$store.getters["user/getRole"];
    },
  },
  methods: {
    goTo(link) {
      this.$router.push(link);
    },

    correctRedirect() {
      const role = this.$store.getters["user/getRole"];

      if (role === "user") {
        this.goTo("/profile");
      } else if (role === "employee") {
        this.goTo("/bid");
      }
    },
  },
};
</script>

