  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 10px;
  background-color: #e9eaee;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.logo {
  width: 25%;
  cursor: pointer;
}

.user {
  font-size: 32px;
  cursor: pointer;
}

.bell {
  font-size: 22px;
