<template>
  <template v-if="needContainer">
    <div :class="styleForComponent">
      <div class="row" :class="{ 'h-100': needFullHeight }">
        <slot />
      </div>
    </div>
  </template>
  <template v-else>
    <div class="row">
      <slot />
    </div>
  </template>
</template>

<script>
export default {
  props: {
    needContainer: {
      type: Boolean,
      reqiured: false,
      default: true,
    },
    fluid: {
      type: Boolean,
      reqiured: false,
      default: false,
    },
    needFullHeight: {
      type: Boolean,
      reqiured: false,
      default: false,
    },
  },
  computed: {
    styleForComponent() {
      return [
        this.fluid ? "container-fluid" : "container",
        this.needFullHeight ? "h-100" : "",
      ].join(" ");
    },
  },
  mounted() {},
};
</script>
