<template>
  <component :is="comp">
    <router-view />
  </component>
</template>

<script>
export default {
  watch: {
    "$route.meta": {
      immediate: true,
      deep: true,
      handler: function (n) {
        if (Object.keys(n).length === 0) return;

        if (n.layer !== undefined) {
          this.nameFile = n.layer;
        }
      },
    },
  },
  computed: {
    comp() {
      if (this.nameFile !== "") {
        return this.nameFile;
      } else {
        return "FullScreen";
      }
    },
  },
  data: () => ({ nameFile: "" }),
  created() {
    this.$store.dispatch("user/init");
    this.$watch("$route.meta", {
      immediate: true,
      deep: true,
      handler: function (n) {
        if (Object.keys(n).length === 0) return;

        if (n.layer !== undefined) {
          this.nameFile = n.layer;
        }
      },
    });
  },
};
</script>

<style>
#app {
  height: 100dvh;
  overflow: auto;
}
</style>
