import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    name: "Main",
    path: "/",
    redirect: "/news",
  },
  {
    path: "/profile",
    name: "Profile",
    component: () => import("../views/ProfileComponent.vue"),
    meta: {
      needAuth: true,
      layer: "HomeLayer",
      role: "user",
    },
  },
  {
    path: "/news",
    name: "News",
    component: () => import("../views/NewsComponent.vue"),
    meta: {
      needAuth: true,
      layer: "HomeLayer",
    },
  },
  {
    path: "/testing",
    name: "Testing",
    component: () => import("../views/TestingComponent.vue"),
    meta: {
      needAuth: true,
      layer: "HomeLayer",
      role: "user",
    },
  },
  {
    path: "/test/:id",
    name: "Test",
    component: () => import("../views/TestView.vue"),
    meta: {
      needAuth: true,
      layer: "FullScreen",
      role: "user",
    },
  },
  {
    path: "/login",
    component: () => import("../views/LoginComponent.vue"),
    meta: {
      layer: "FullScreen",
    },
  },
  {
    path: "/lessons",
    component: () => import("../views/LessonsComponent.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "user",
    },
  },
  {
    path: "/mentor/:id",
    component: () => import("../views/MentorUser.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "user",
    },
  },
  {
    path: "/trainee-select",
    component: () => import("../views/SelectTraineeComponent.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "user",
    },
  },
  {
    path: "/trainee-item/:id",
    component: () => import("../views/TraineeItemComponent.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "user",
    },
  },
  {
    path: "/bid",
    component: () => import("../views/Employee/EmployeeComponent.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "employee",
    },
  },
  {
    path: "/bid/:id",
    component: () => import("../views/Employee/TraineeHomeScreen.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "employee",
    },
  },
  {
    path: "/bid/:id/set-mentor",
    component: () => import("../views/Employee/SetMentor.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "employee",
    },
  },
  {
    path: "/bid/:id/list-trainee",
    component: () => import("../views/Employee/SetTrainee.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "employee",
    },
  },
  {
    path: "/create-trainee",
    component: () => import("../views/Employee/EmployentCreateTrainee.vue"),
    meta: {
      layer: "HomeLayer",
      needAuth: true,
      role: "employee",
    },
  },
  {
    path: "/:pathMatch(.*)*",
    component: () => import("../views/404Component.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.beforeEach((to, _, next) => {
  if (to.meta.needAuth && !router.$store.getters["user/isAuth"]) {
    return next("/login");
  }

  if (
    to.meta.role !== undefined &&
    to.meta.role !== router.$store.getters["user/getRole"]
  ) {
    return next("/news");
  }
  next();
});

export default router;
