export default {
  namespaced: true,
  state: {
    user: null,
  },
  getters: {
    isAuth(state) {
      return state.user !== null;
    },
    isUserAploadDocument(state) {
      return state.user?.uploadDocument !== null;
    },
    getRole(state) {
      return state.user?.role;
    },
  },
  mutations: {
    setUser(state, user) {
      state.user = user;
    },
  },
  actions: {
    init({ commit }) {
      let role = localStorage.getItem("role");

      if (role === null) {
        role = "user";
      }

      if (role !== null) {
        commit("setUser", {
          id: 1,
          role: role,
        });
      }
    },
  },
};
